import '../App.css';
import Dog from './Dogs';
import Pagination from './Pagination';

const DogList = (props) => {



    const dogsArray = props.dogs.map((dogURL) => {
        
        let randomName = require('dog-names')
        let dogMaleName = randomName.maleRandom()
        let dogFemaleName = randomName.femaleRandom()
        let quote = quotes[getRandomInt(quotes.length)];
        let date = dates[getRandomInt(dates.length)]

        return <Dog
            url={dogURL}
            dogMaleName={dogMaleName}
            quote={quote}
            dogFemaleName={dogFemaleName}
            date={date}
        />
    });

    return (
        <div className="content">
            <div className="news flex-flow-row-wrap">
                {dogsArray}
                <Pagination />
            </div>
        </div>

    )

};


export default DogList;



